@import './Variables.scss';
@import './Global.scss';

.offer-container {
    margin-top: 100vh;
    width: 100%;

    .offer-txt {
        margin-top: 100px;

        h1 {
            text-align: center;
            margin-bottom: 0;
            color: $third-color;
        }

        hr {
            width: 10%;
            margin-top: 0;
        }
    }

    .offer-icons {
        margin-top: 80px;

        .row {
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-top: 100px;
            margin-left: 20%;
            margin-right: 20%;

            .icon-l {
                order: 1;

                img {
                    height: 300px;
                }
            }

            .icon-r {

                order: 2;

                img {
                    height: 300px;
                }

            }

            .txt-r {
                width: 50%;
                order: 2;
                font-family: 'Roboto', sans-serif;
                font-weight: 100;

            }

            .txt-l {
                width: 50%;
                order: 1;
                font-family: 'Roboto', sans-serif;
                font-weight: 100;

            }

            svg {
                color: #84a98c;
            }

            p,
            li {
                text-align: justify;
                line-height: 1.5em;

            }
        }
    }
}

// ============================ RES STYLES =================================
@media screen and (max-width: 775px) {
    .offer-container {
        .offer-icons {
            .row {
                flex-wrap: wrap;
                justify-content: center;
                margin-left: 0;
                margin-right: 0;
            }

            .icon-l {
                width: 100%;
                display: flex;
                justify-content: center;
            }

            .icon-r {
                width: 100%;
                order: 1 !important;
                display: flex;
                justify-content: center;
            }

            .txt-l {
                width: 100%;
            }
            .txt-r {
                width: 100%;
            }
        }
    }
}
