@import './Variables.scss';
@import './Global.scss';

.HeaderSection {
    margin-bottom: 100px;

    .Navigation {
        height: auto;
        position: absolute;
        top: 20px;
        z-index: 1;
        right: 20px;

        ul {

            li {
                display: inline-block;

                a {
                    right: 0;
                    text-decoration: none;
                    font-weight: 600;
                    font-size: 20px;
                    color: $third-color;
                    padding: 25px;
                }

                a:hover {
                    text-decoration: underline;
                }

            }
        }
    }

    .Logo-container {
        .bg {
            clip-path: polygon(0 0, 100% 0%, 75% 100%, 0% 100%);
            background-color: $base-color;
            width: 70%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            z-index: 0;
        }

        .svg {
            width: 35%;
            z-index: 1;
            position: absolute;
            left: 20px;
            top: 5%;
        }

        img {
            width: 60%;
            top: 30%;
            z-index: 1;
            position: absolute;
            right: 0;
        }

        .text-zone {
            z-index: 1;
            color: $first-color;
            position: absolute;
            width: 35%;
            top: 70%;
            text-align: right;
            left: 5%;

            h1 {
                font-family: $base-font;
                font-weight: 400;
                font-size: 1.5em;
            }

            p {
                font-family: $second-font;
                font-weight: 100;
            }
        }
    }

}

// ============================ RES STYLES =================================

@media screen and (max-width: 1285px) {
    .HeaderSection {
        .Navigation {
            ul {
                li {
                    a {
                        font-size: 15px;
                        padding: 10px;
                    }
                }
            }
        }

        .Logo-container {
            img {
                width: 50%;
                top: 30%;
                z-index: 1;
                position: absolute;
                right: 0;
            }

            .text-zone {
                top: 70%;
                width: 45%;
            }
        }
    }
}

@media screen and (max-width: 1025px) {
    .Logo-container {
        img {
            width: 50% !important;
        }
    }
}

@media screen and (max-width: 775px) {
    .HeaderSection {
        .Navigation {
            display: none;
        }

        .Logo-container {
            .bg {
                display: none
            }

            .bg-mobile {
                clip-path: polygon(0 100%, 100% 0, 0 0);
                background-color: $base-color;
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
                z-index: 0;
            }

            .svg {
                width: 40%;
            }

            img {
                display: none;
            }

            .text-zone {
                width: 50%;
                text-align: left;
                color: $third-color;
                left: initial;
                right: 5%;
                top: 60%;

                p {
                    color: black;
                }
            }
        }
    }
}

@media screen and (max-width: 600px) {
    .HeaderSection {
        .text-zone {
            width: 60%;

            h1 {
                text-align: right;
            }

        }
    }
}

@media screen and (max-width: 490px) {
    .HeaderSection {
        .Logo-container {
            .bg-mobile {
                display: none;
            }

            .bg-mobile-s {
                clip-path: polygon(0 0, 50% 40%, 100% 0);
                background-color: $base-color;
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;
                left: 0;
                z-index: 0;
            }

            .svg {
                left: 0;
                right: 0;
                margin-left: 25%;
                width: 40%;
                top: 0;
            }
        }

        .text-zone {
            right: 0 !important;
            left: 0 !important;
            margin-right: auto;
            margin-left: auto;
            width: 80% !important;

            // top: 40% !important;
            h1 {
                text-align: left;
            }
        }
    }
}